.header-cart {

    display: flex;

    &__imgs {
        display: flex;
        position: relative;
        margin-right: 24px;
    }

    &__quantity {
        position: absolute;
        padding: 0 2px;
        top: -8.5px;
        right: -12.5px;
        min-width: 25px;
        min-height: 25px;
        background: #FFC85E;
        border: 3px solid #FFFFFF;
        border-radius: 15px;

        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #FFFFFF;
    }

    &__sum {
        display: flex;
        flex-direction: column;
        align-items: center;

        font-style: normal;
        font-weight: 300;
        font-size: 12px;
        line-height: 150%;
        color: #3F4E65;

        span {
            font-style: normal;
            font-weight: 600;
            font-size: 14px;
            line-height: 150%;
            color: #111111;
        }
    }

    &__bread-crumbs {}
}

.bread-crumbs {
    max-width: 1370px;
    margin: 45px auto 65px;
    padding: 0 10px;
    // margin: 45px 0 65px 0;
    display: flex;

    a {
        color: #3F4E65;

    }

    &__item {
        font-family: 'Inter';
        font-style: normal;
        font-weight: 300;
        font-size: 14px;
        line-height: 120%;
        padding: 0 10px;
        color: #3F4E65;
    }

    &__item:first-child {
        padding: 0 10px 0 0;
    }

    &__item:last-child {
        padding: 0 0 0 10px;
        opacity: 0.5;
    }

    &__item:not(:last-child) {
        border-right: 1px dashed rgba(63, 78, 101, 0.3);
    }
}