.header {
	&__top-line {
		padding: 10px 0;
		border-bottom: 1px solid rgba(63, 78, 101, .2);
	}

	&__inner-container {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
	}

	&__start {}

	&__end {
		margin-left: auto;
	}

	&__main {
		border-bottom: 1px solid rgba(63, 78, 101, .2);
	}

	&__main-inner {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		overflow: hidden;
		padding: 8px 0;
	}

	&__main-general {
		flex-grow: 1;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		//margin-right: 15px;
		padding: 8px 0;
	}

	&__info {
		padding: 8px 0;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-grow: 1;
		justify-content: flex-start;
		margin-left: 35px;
	}

	&__info-item {
		position: relative;
		padding-left: 25px;
		margin-left: 25px;

		&:before {
			content: '';
			display: block;
			position: absolute;
			left: 0;
			top: 50%;
			transform: translateY(-50%);
			width: 1px;
			height: 49px;
			background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' stroke='%233F4E651A' stroke-width='1' stroke-dasharray='2%2c 2' stroke-dashoffset='0' stroke-linecap='butt'/%3e%3c/svg%3e");
		}

		&:first-child {
			margin-right: auto;
		}

		&:first-child {
			padding-left: 0;
			margin-left: 0;

			&:before {
				content: none;
			}
		}
	}

	&__logo {
		margin-right: 38px;
	}

	&__search {
		flex-grow: 1;
	}

	@media only screen and (max-width: 1100px) {
		&__start {
			width: 100%;
			margin-bottom: 15px;
		}

		&__end {
			width: 100%;
		}

		.menu__list {
			justify-content: center;
		}
	}

	@media only screen and (max-width: 768px) {

		&__top-line,
		&__main {
			display: none;
		}

	}

}

.header-mobi {
	display: none;

	@media only screen and (max-width: 768px) {
		display: flex;
		flex-direction: column;
		max-width: 100%;


		&__main {
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding: 10px 15px;
			border-bottom: 1px solid rgba(63, 78, 101, 0.2);
		}

		&__line {
			display: flex;
		}

		&__btn {
			@include button-reset;
			position: relative;
			flex: 0 0 50%;
			max-width: 50%;
			width: 50%;
			display: flex;
			align-items: center;
			justify-content: center;
			padding: 12px 10px;

			&:before {
				content: '';
				display: block;
				position: absolute;
				right: 0;
				top: 50%;
				transform: translateY(-50%);
				width: 1px;
				height: 30px;
				background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' stroke='%233F4E651A' stroke-width='1' stroke-dasharray='2%2c 2' stroke-dashoffset='0' stroke-linecap='butt'/%3e%3c/svg%3e");
			}

			&:last-child {
				&:before {
					content: none;
				}
			}

		}

		&__btn-icon {
			display: block;
			flex-shrink: 0;
			margin-right: 6px;
		}

		&__btn-text {
			display: block;
			white-space: nowrap;
			color: #3F4E65;
			font-weight: 600;
			font-size: 12px;
			line-height: 1.25;
			overflow: hidden;
			text-overflow: ellipsis;
		}
	}
}