.main-product-body {
    display: flex;
    justify-content: space-between;
    padding-top: 20px;

    // .main-product-body__aside

    &__aside {
        width: 238px;
        padding-right: 15px;
    }

    // .main-product-body__rows

    &__rows {}

    @media (max-width:560px) {
        flex-direction: column;
        justify-content: center;
        align-items: center;

        &__aside {
            width: 100%;
            padding-right: 0;
        }
    }
}

.aside {
    &__title {
        font-weight: 500;
        font-size: 16px;
        line-height: 150%;
        margin-bottom: 10px;
    }

    // .aside__price

    &__price {}

    @media (max-width:560px) {
        &__title {
            text-align: center;
            margin-bottom: 20px;
        }

        // .aside__price

        &__price {

            text-align: center;
        }
    }
}

.main-product {

    // .main-product__title

    &__title {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 25px;

        h1 {
            font-family: 'Inter';
            font-style: normal;
            font-weight: 500;
            font-size: 40px;
            line-height: 120%;
            /* identical to box height, or 48px */
            text-transform: uppercase;
            color: #111111;
        }

        @media (max-width:860px) {
            display: flex;
            justify-content: space-between;
            align-items: center;
            flex-direction: column;
            margin-bottom: 25px;

            h1 {
                margin-bottom: 35px;
                text-align: center;
            }
        }
    }

    // .main-product__sorting

    &__sorting {}

    &__categories {
        margin-bottom: 30px;

        .categories {
            ul {
                display: flex;

                text-align: center;

                li {
                    font-size: 14px;
                    line-height: 17px;
                    background-color: #f9f9f9;
                    padding: 15px 15px;
                    border-radius: 20px;
                    margin-right: 10px;
                    color: #3F4E65;
                    cursor: pointer;
                    transition: background-color 0.1s ease-in-out;

                    &:hover {
                        background-color: darken(#f9f9f9, 2%);
                    }

                    &:active {
                        background-color: darken(#f9f9f9, 5%);
                    }

                    &.active {
                        background-color: #4b4a4a;
                        color: #fff;
                    }

                }


            }
        }

        @media (max-width:860px) {
            .categories {
                ul {
                    display: flex;
                    flex-wrap: wrap;
                    text-align: center;
                    justify-content: center;

                    li:last-child {
                        margin-right: 0;
                    }

                    li {
                        margin-bottom: 5px;
                    }
                }

            }
        }

        &__body {}
    }
}

.main-product-item {
    display: flex;

    padding: 20px 0 0 0;

    @media (max-width:920px) {
        flex-direction: column;
        align-items: center;

    }

    &__img {
        flex: 1 0 45%;
        margin-right: 40px;
        // width: 664px;
        // height: 471px;
        display: flex;
        align-items: center;
        justify-content: center;

        object-fit: cover;

        img {
            max-width: 100%;
            max-height: 100%;

        }
    }

    @media (max-width:920px) {

        &__img {
            // flex: 1 0 45%;
            margin-right: 0;
            width: 264px;
        }
    }


    &__body {}

    &__status {
        font-family: 'Inter';
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 130%;
        color: #1FD85D;
        margin-bottom: 10px;
    }

    &__name {
        font-family: 'Inter';
        font-style: normal;
        font-weight: 800;
        font-size: 30px;
        line-height: 150%;

        margin-bottom: 10px;
        color: #111111;
    }

    &__volume {
        font-family: 'Inter';
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 130%;

        color: #3F4E65;
        margin-bottom: 40px;
    }

    &__line {
        display: flex;
        justify-content: space-between;
        margin-bottom: 40px;
    }

    &__line :not(:last-child) {
        // margin-right: 40px;

    }

    @media (max-width:970px) {
        &__line {
            display: flex;
            justify-content: space-between;
            flex-direction: column;
            margin-bottom: 40px;
        }

        &__line>:not(:last-child) {
            margin-bottom: 40px;

        }

    }


    &__price {
        font-family: 'Inter';
        font-style: normal;
        font-weight: 800;
        font-size: 30px;
        line-height: 130%;
        color: #111111;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__counter {
        display: flex;
        justify-content: center;
        align-items: center;
        padding-left: 44px;

        height: 49px;
    }

    @media (max-width:970px) {
        &__counter {
            display: flex;
            justify-content: center;
            align-items: center;
            padding-left: 0;

            height: 49px;
        }

    }

    &__reduce {
        font-style: normal;
        font-weight: 300;
        font-size: 12px;
        line-height: 15px;
        text-align: center;
        color: #3F4E65;

        background: linear-gradient(90deg, rgba(255, 198, 80, 0.3) 0%, rgba(254, 202, 110, 0.3) 97.25%);
        border-radius: 1000px;
        padding: 4px 14px;
        margin-right: 17px;
    }

    &__number {
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 130%;
        color: #3F4E65;
        margin-right: 21px;
    }

    &__add {
        font-style: normal;
        font-weight: 300;
        font-size: 12px;
        line-height: 15px;
        text-align: center;
        color: #3F4E65;

        background: linear-gradient(90deg, rgba(255, 198, 80, 0.3) 0%, rgba(254, 202, 110, 0.3) 97.25%);
        border-radius: 1000px;
        padding: 4px 14px;
    }

    &__clear {
        height: 49px;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__description {
        margin-top: 30px;
        margin-bottom: 30px;

        h3 {
            font-family: 'Inter';
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 150%;
            color: #111111;
            margin-bottom: 15px;
        }

        span {
            font-family: 'Inter';
            font-style: normal;
            font-weight: 300;
            font-size: 12px;
            line-height: 150%;
            color: #3F4E65;
        }
    }
}